<template>
  <div class="edit-do-time">
    <div class="item">
      <em>营业日期</em>
      <cube-checkbox-group v-model="checkList" :options="options" :horizontal="true" :shape="'square'" />
    </div>
    <div class="item">
      <em>营业时间</em>
      <p>
        <span @click="showMutiPicker">8:00</span><i>至</i><span>21:00</span>
      </p>
    </div>
    <cube-button :outline="true">保存</cube-button>
  </div>
</template>

<script>
const hour = []
const minute = []

for (let i = 1; i < 25; i++) {
  hour.push({ text: `${i}点`, value: i })
}

for (let i = 1; i < 61; i++) {
  minute.push({ text: `${i}分`, value: i })
}

export default {
  data () {
    return {
      checkList: [],
      options: ['日', '一', '二', '三', '四', '五', '六'],
      hourList: hour,
      minuteList: minute
    }
  },
  methods: {
    showMutiPicker () {
      if (!this.mutiPicker) {
        this.mutiPicker = this.$createPicker({
          data: [this.hourList, this.minuteList],
          onSelect: this.selectHandle
        })
      }
      this.mutiPicker.show()
    },
    selectHandle (selectedVal, selectedIndex, selectedText) {
      this.$createDialog({
        type: 'warn',
        content: `Selected Item: <br/> - value: ${selectedVal.join(', ')} <br/> - index: ${selectedIndex.join(', ')} <br/> - text: ${selectedText.join(' ')}`,
        icon: 'cubeic-alert'
      }).show()
    }
  }
}
</script>

<style lang="stylus" scoped>
.edit-do-time {
  padding: 20px;
  .item {
    margin-bottom: 20px;
    em {
      line-height: 50px;
    }
    p {
      display: flex;
      line-height: 40px;
      margin-bottom: 60px;
      span {
        width: 100px;
        text-align: center;
        border-bottom: 2px solid #ccc;
      }
    }
  }
}
</style>



